import React from 'react'
import Mytab2 from './Mytab2'
import {Icon} from 'antd-mobile';

const Details =(props)=> {
      return (<div>
        <Icon 
            type="left" 
            onClick={() => props.history.push('/home')}
            style={{borderRadius:'20px',
                backgroundColor:'black',
                opacity:0.5,
                color:'white',
                zIndex:'2',
            }}
        />
        <img
            src={`https://g-search3.alicdn.com/img/bao/uploaded/i4/i1/2271051490/O1CN01kFD3xD1MsSiw69wKE_!!0-item_pic.jpg_580x580Q90.jpg_.webp`}
            alt=""
            style={{ width: '100%',zIndex:'1',marginTop:'-25px'}}
        />
        <Mytab2 />
      </div>);
    }
export default Details