import React from 'react';
import {TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
            <TabBar unselectedTintColor="#949494" tintColor="black" barTintColor="white">
                <TabBar.Item
                    title="店铺"
                    key="shop"
                    icon={ <i className='iconfont icon-shop'></i> }
                    data-seed="logId"
                >
                </TabBar.Item>
                <TabBar.Item
                    icon={<i className='iconfont icon-wang'></i>  }
                    title="客服"
                    key="customer"
                    data-seed="logId1"
                ></TabBar.Item>
                <TabBar.Item
                    icon={<i className='iconfont icon-favor'></i>}
                    title="收藏"
                    key="collect"
                >
			    </TabBar.Item>
                <TabBar.Item
                    icon={<div 
                        style={{borderRadius:'15px 0px 0px 15px',
                        width:'100px',
                        height:'30px',
                        backgroundColor:'orange',
                        color:'white',
                        fontWeight:'700'
                    }}><p style={{marginTop:'6px'}}>加入购物车</p></div> } 
                    key="addtocart"
                >
			    </TabBar.Item>	
                <TabBar.Item
                    icon={
                    <div 
                        style={{borderRadius:'0px 15px 15px 0px',
                        width:'100px',
                        height:'30px',
                        backgroundColor:'red',
                        color:'white',
                        fontWeight:'700',
                        marginRight:'20px'
                    }}><p style={{marginTop:'6px'}}>立即购买</p></div> }
                    
                    key="buynow"
                >
			    </TabBar.Item>
		  </TabBar>
		</div>
	  );
	}
  }

export default withRouter(Mytab2);